.inputStoryForm{
    margin:0px 30px 30px 30px;
    padding: 70px 0px 0px 0px;

   
}
.StoryButton{
    margin-top: 5px;
     align-items: center;
     position: absolute;
     right: 30px;
 
 }
 
 
 .Input{
     width:100%;
     padding:10px;
     box-sizing: border-box;
 }
 .Label{
     font-weight: bold;
     display: block;
     margin-bottom: 8px;
 }
 .InputElement{
     outline:none;
     border:1px solid #ccc;
     background-color: white;
     font: inherit;
     padding: 6px 10px;
     display:block;
     width:100%;
     box-sizing: border-box;
 }
 .InputElement:focus{
     outline:none;
     background-color:#ccc;
 }
 .Invalid {
    border: 1px solid red;
    
 }
 .editStoryForm{
    border:1px solid #ccc;
    background: rgba(204, 204, 204, 0.103);
    font: inherit;
    padding: 10px 10px;
    display:block;
    width:100%;

}
.modalButton{
    margin:0px 10px 5px 10px;
}

